import React from "react";
import Layout from "./Layout";
import { SEO } from "theme-seo";
import { Link } from "gatsby";
import { GetCities, provinces } from "./Iran";
import { GetBackendUrl } from "theme-library";

const Profile = ({ pageContext }) => {
  let parseServerUrl = pageContext.parseServerUrl;
  const backendUrl = GetBackendUrl();
  if (backendUrl !== null) parseServerUrl = backendUrl;

  const urlParams = new URLSearchParams(
    typeof window !== "undefined" ? window.location.search : ""
  );
  const returnUrl = urlParams.get("returnUrl");

  const [formISValid, setFormValidation] = React.useState(true);

  const [user, setUser] = React.useState({
    Name: "",
    FamilyName: "",
    CodeMelli: "",
    username: "",
    Ostan: "",
    Shahr: "",
    Moaref: "",
    NahveAshnaei: "",
    Senf: "",
    Address: "",
    CodePosti: "",
    CodeEghtesadi: "",
    ShenaseMelli: "",
    NameSherkat: "",
  });
  const [invoices, setInvoice] = React.useState([]);
  const [states, setStates] = React.useState([]);
  const [cities, setCities] = React.useState([]);
  const [nahveAshnaei, setNahveAshnaei] = React.useState([]);
  const [asnaf, setAsnaf] = React.useState([]);
  const [isEditing, setIsEditing] = React.useState(returnUrl !== null);

  const claims = getCookie("Claims");
  const sessionToken = claims ? JSON.parse(decodeURIComponent(claims)).SessionToken : null;

  const multiCashDownloads = [
    { title: "دانلود نسخه کلاینت ", url: pageContext.downloadsUrl + "Setup-Server.zip" },
    {
      title: "دانلود نسخه کلاینت با پیش نیاز ",
      url: pageContext.downloadsUrl + "Setup-Server-With-PreRequirement.zip",
    },
    { title: "دانلود نسخه سرور ", url: pageContext.downloadsUrl + "Setup-Server.zip" },
    {
      title: "دانلود نسخه سرور با پیش نیاز ",
      url: pageContext.downloadsUrl + "Setup-Server-With-PreRequirement.zip",
    },
  ];

  const singleCashDownloads = [
    { title: "دانلود نسخه تک صندوقه ", url: pageContext.downloadsUrl + "Setup-Local.zip" },
    {
      title: "دانلود نسخه تک صندوقه با پیش نیاز ",
      url: pageContext.downloadsUrl + "Setup-Local-With-PreRequirement.zip",
    },
  ];
  React.useEffect(function () {
    if (sessionToken) {
      fetch(parseServerUrl + "parse/Users/me", {
        headers: {
          "X-Parse-Session-Token": `${sessionToken}`,
          "X-Parse-Application-Id": `${pageContext.applicationId}`,
        },
      })
        .then(function (results) {
          return results.json();
        })
        .then(function (r) {
          let newUser = {};
          newUser.objectId = r.objectId;
          newUser.Name = r.Name || "";
          newUser.FamilyName = r.FamilyName || "";
          newUser.CodeMelli = r.CodeMelli || "";
          newUser.username = r.username || "";
          newUser.Ostan = r.Ostan || "";
          newUser.Shahr = r.Shahr || "";
          newUser.Moaref = r.Moaref || "";
          newUser.NahveAshnaei = r.NahveAshnaei || "";
          newUser.Senf = r.Senf || "";
          newUser.Address = r.Address || "";
          newUser.NameSherkat =  r.NameSherkat || "" ;
          newUser.ShenaseMelli = r.ShenaseMelli || "";
          newUser.CodePosti = r.CodePosti || "";
          newUser.CodeEghtesadi = r.CodeEghtesadi || "";

          setUser(newUser);

          SetAllStates(newUser);

          SetAllNahveAshnaei();
          SetAllAsnaf();
        })
        .then(function () {
          fetch(parseServerUrl + "parse/classes/Invoice", {
            headers: {
              "X-Parse-Application-Id": `${pageContext.applicationId}`,
              "X-Parse-Session-Token": `${sessionToken}`,
            },
          })
            .then(function (response) {
              return response.json();
            })
            .then(function (result) {
              if (result.error === "Invalid session token") {
                window.location = pageContext.authUrl + "?returnUrl=" + window.location;
              }

              fetch(parseServerUrl + "parse/classes/Option", {
                headers: {
                  "X-Parse-Application-Id": `${pageContext.applicationId}`,
                },
              })
                .then(function (response) {
                  return response.json();
                })
                .then(function (r) {
                  let successInvoices = result.results
                    .filter((invoice) => invoice.paymentResult !== undefined)
                    .sort((a, b) => new Date(b.Date) - new Date(a.Date))
                    .map((invoice) => {
                      if (invoice.Options) {
                        const options = r.results.filter((option) =>
                          invoice.Options.split(",").includes(option.objectId)
                        );
                        invoice.EmkanatJanebi = options.map((op) => op.Title);
                      }
                      try {
                        invoice.ShenasehFaalSazi = JSON.parse(invoice.ShenasehFaalSazi);
                      } catch (e) {
                        invoice.ShenasehFaalSazi = {
                          "-": invoice.ShenasehFaalSazi ? invoice.ShenasehFaalSazi : "ایجاد نشده",
                        };
                      }
                      return invoice;
                    });
                  setInvoice(successInvoices);
                });
            });
        });
    }
  }, []);

  function getCookie(name) {
    if (typeof document === "undefined") return;
    const value = `; ${document.cookie}`;
    const parts = value.split(`; ${name}=`);
    if (parts.length === 2) return parts.pop().split(";").shift();
  }

  function Edit() {
    setIsEditing(true);
  }

  function Cancel() {
    setIsEditing(false);
  }

  function Save() {
    if (returnUrl && !user.CodeMelli && !user.CodeEghtesadi) {
      setFormValidation(false);
      return;
    } else if (
      returnUrl &&
      user.CodeMelli &&
      (user.Name === "" ||
        user.Shahr === "" ||
        user.Ostan === "" ||
        user.FamilyName === "" ||
        user.username === "" ||
        user.Senf === "")
    ) {
      setFormValidation(false);
      return;
    } else if (
      returnUrl &&
      user.CodeEghtesadi &&
      (user.ShenaseMelli === "" ||
        user.Address === "" ||
        user.NameSherkat === "" ||
        user.CodePosti === "")
    ) {
      setFormValidation(false);
      return;
    } else setFormValidation(true);

    fetch(parseServerUrl + "/parse/Users/" + user.objectId, {
      method: "PUT",
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
        "X-Parse-Session-Token": `${sessionToken}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(user),
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (result) {
        setIsEditing(false);
        window.RefreshToken(pageContext.authUrl);
        if (returnUrl) {
          setTimeout(() => {
            window.location = returnUrl;
          }, 4000);
        }
      });
  }

  function handleInputChange(event) {
    let value = event.target.value;
    if (
      event.target.name === "CodeMelli" ||
      event.target.name === "username" ||
      event.target.name === "CodeEghtesadi" ||
      event.target.name === "CodePosti" ||
      event.target.name === "ShenaseMelli"
    ) {
      if (value !== "" && !/^[0-9\b]+$/.test(value)) {
        return;
      }
    }

    const clonedUser = { ...user };
    clonedUser[event.target.name] = value.toString();
    setUser(clonedUser);
  }

  function NumberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  function SetAllStates(user) {
    provinces.unshift({ id: "*", name: "" });
    setStates(provinces);
    if (user.Ostan) {
      let stateId = provinces.filter((s) => s.name === user.Ostan)[0].id;
      SetRelatedCities(stateId, user.Shahr);
    }
  }

  function SetRelatedCities(id) {
    let stateId = id;
    if (!id) {
      let stateName = document.getElementById("states").value;

      const clonedUser = { ...user };
      clonedUser["Ostan"] = stateName;
      clonedUser["Shahr"] = "";
      setUser(clonedUser);

      if (stateName) stateId = states.filter((s) => s.name === stateName)[0].id;
    }
    if (stateId) {
      let cities = GetCities(stateId);
      cities.unshift({ id: "*", name: "" });
      setCities(cities);
    } else {
      setCities([]);
    }
  }

  function SetAllNahveAshnaei() {
    fetch(parseServerUrl + "parse/classes/NahveAshnaei", {
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (response) {
        let ways = response.results.map((way) => way.Title);
        ways.unshift("");
        setNahveAshnaei(ways);
      });
  }

  function SetAllAsnaf() {
    fetch(parseServerUrl + "parse/classes/Asnaf", {
      headers: {
        "X-Parse-Application-Id": `${pageContext.applicationId}`,
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (response) {
        let allAsnaf = response.results.map((senf) => senf.Title);
        allAsnaf.unshift("");
        setAsnaf(allAsnaf);
      });
  }

  const hiddenFileInput = React.useRef(null);
  
  const handleClick = event => {
    hiddenFileInput.current.click();
  };

  function download(dataurl, filename) {
    var a = document.createElement("a");
    a.href = dataurl;
    a.setAttribute("download", filename);
    a.click();
    return false;
}

function submit() {
  console.log("submit");
    var formData = new FormData();
    var imagefile = document.querySelector('#file');
    formData.append("file", imagefile.files[0]);

    formData.getAll('file')[0].text().then(contents => {

        fetch('https://licensing.raymasoft.com/LicenseService.svc/License', {
                method: "POST",
                body: contents,
        })
        .then(res => res.text())
        .then(function(text){
            download("data:text/plain," + text, "activationCode.rsf")
        });
    })
    
}
  const validationHaghighi = !formISValid && user.CodeMelli && returnUrl;
  const validationHoghoghi = !formISValid && user.CodeEghtesadi && returnUrl;
  return (
    <Layout>
      <SEO title={pageContext.title} />
      <div className="my-8 p-2 mx-auto">
        {returnUrl && (
          <div className="my-10 text-gray-600 font-bold w-4/5 mx-auto">
            <label>تکمیل اطلاعات کاربری جهت ادامه فرایند خرید الزامی است.</label>
          </div>
        )}

        <form className="text-gray-600 my-10 mx-auto w-11/12 lg:w-3/4">
          <fieldset className="shadow-md border-gray-300 border-2 border-double p-8 pt-4 pr-4">
            <legend className="text-xl font-bold">اطلاعات حقیقی</legend>
            {!formISValid && returnUrl && (user.CodeMelli || user.CodeEghtesadi) && (
              <label className="text-sm text-red-800">
                پر کردن فیلد های ستاره دار الز امی است.
              </label>
            )}
            {!formISValid && returnUrl && !user.CodeMelli && !user.CodeEghtesadi && (
              <label className="text-sm text-red-800">
                جهت ادامه فرایند خرید پرکردن فیلد کد ملی برای شخص حقیقی و یا کد اقتصادی برای شخص
                حقوقی الزامی است.
              </label>
            )}
            <div className="flex flex-col lg:flex-row">
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHaghighi || validationHoghoghi) && user.Name === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  نام
                  <span className="font-medium">{!isEditing ? " :  " + user.Name : ""}</span>
                  {(validationHaghighi || validationHoghoghi) && user.Name === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <input
                    className={
                      ((validationHaghighi || validationHoghoghi) && user.Name === ""
                        ? "border-red-700"
                        : "") +
                      " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                    }
                    value={user.Name}
                    onChange={handleInputChange}
                    name={"Name"}
                  />
                )}
              </div>
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHaghighi || validationHoghoghi) && user.FamilyName === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  نام خانوادگی
                  <span className="font-medium">{!isEditing ? " :  " + user.FamilyName : ""}</span>
                  {(validationHaghighi || validationHoghoghi) && user.FamilyName === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <input
                    className={
                      ((validationHaghighi || validationHoghoghi) && user.FamilyName === ""
                        ? "border-red-700"
                        : "") +
                      " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                    }
                    value={user.FamilyName}
                    onChange={handleInputChange}
                    name={"FamilyName"}
                  />
                )}
              </div>
            </div>
            <div className="flex flex-col lg:flex-row mt-4">
              <div className="w-full mx-3">
                <label
                  className={
                    (!formISValid && user.CodeEghtesadi === "" && returnUrl && user.CodeMelli === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  کد ملی
                  <span className="font-medium">{!isEditing ? " :  " + user.CodeMelli : ""}</span>
                  {!formISValid &&
                    user.CodeEghtesadi === "" &&
                    returnUrl &&
                    user.CodeMelli === "" && <span className="text-red-800 m-1">*</span>}
                </label>
                {isEditing && (
                  <input
                    className={
                      (!formISValid &&
                      user.CodeEghtesadi === "" &&
                      returnUrl &&
                      user.CodeMelli === ""
                        ? "border-red-700"
                        : "") +
                      " shadow appearance-none border rounded w-full py-2 px-3  leading-tight focus:outline-none focus:shadow-outline"
                    }
                    value={user.CodeMelli}
                    onChange={handleInputChange}
                    name={"CodeMelli"}
                  />
                )}
              </div>
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHaghighi || validationHoghoghi) && user.username === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  شماره موبایل
                  <span className="font-medium">{!isEditing ? " :  " + user.username : ""}</span>
                  {(validationHaghighi || validationHoghoghi) && user.username === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <input
                    className={
                      ((validationHaghighi || validationHoghoghi) && user.username === ""
                        ? "border-red-700"
                        : "") +
                      " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                    }
                    value={user.username}
                    onChange={handleInputChange}
                    name={"username"}
                  />
                )}
              </div>
            </div>
            <div className="flex flex-col lg:flex-row mt-4">
              <div className="w-full mx-3">
                <label
                  className={
                    (validationHaghighi && !validationHoghoghi && user.NameSherkat === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  نام واحد صنفی
                  <span className="font-medium">
                    {!isEditing ? " :  " + (user.CodeMelli !== "" ? user.NameSherkat || "" : "") : ""}
                  </span>
                  {validationHaghighi && !validationHoghoghi && user.NameSherkat === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <input
                    className={
                      (validationHaghighi && !validationHoghoghi && user.NameSherkat === ""
                        ? "border-red-700"
                        : "") +
                      " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                    }
                    value={(user.CodeMelli !== "" ? user.NameSherkat || "" : "")}
                    onChange={handleInputChange}
                    name={"NameSherkat"}
                  />
                )}
              </div>
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHoghoghi || validationHaghighi) && user.Senf === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2 "
                  }
                >
                  صنف
                  <span className="font-medium">{!isEditing ? " :  " + user.Senf : ""}</span>
                  {(validationHoghoghi || validationHaghighi) && user.Senf === "" && (
                    <span className="text-red-800 m-1">*</span>
                  )}
                </label>
                {isEditing && (
                  <select
                    name="Senf"
                    id="Senf"
                    onChange={handleInputChange}
                    value={user.Senf}
                    className={
                      ((validationHoghoghi || validationHaghighi) && user.Senf === ""
                        ? "border-red-700"
                        : "") + " w-full border shadow rounded h-10 focus:shadow-outline"
                    }
                  >
                    {asnaf.length > 0 &&
                      asnaf.map((s) => (
                        <option className="hover:bg-teal-500" key={s} id={s} value={s}>
                          {s}
                        </option>
                      ))}
                  </select>
                )}
              </div>
            </div>
            <div className="flex flex-col lg:flex-row mt-4">
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHaghighi || validationHoghoghi) && user.Ostan === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2"
                  }
                >
                  استان
                  <span className="font-medium">{!isEditing ? " :  " + user.Ostan : ""}</span>
                  {!formISValid && user.CodeMelli && user.Ostan === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <select
                    id="states"
                    name="states"
                    onChange={() => SetRelatedCities(null, null)}
                    value={user.Ostan}
                    className={
                      ((validationHaghighi || validationHoghoghi) && user.Ostan === ""
                        ? "border-red-700"
                        : "") + " w-full border shadow rounded h-10 focus:shadow-outline"
                    }
                  >
                    {states.map((s) => (
                      <option className="hover:bg-teal-500" key={s.id} id={s.id} value={s.name}>
                        {s.name}
                      </option>
                    ))}
                  </select>
                )}
              </div>
              <div className="w-full mx-3">
                <label
                  className={
                    ((validationHaghighi || validationHoghoghi) && user.Shahr === ""
                      ? "text-red-800"
                      : "") + " block font-bold my-2"
                  }
                >
                  شهر
                  <span className="font-medium">{!isEditing ? " :  " + user.Shahr : ""}</span>
                  {(validationHaghighi || validationHoghoghi) && user.Shahr === "" ? (
                    <span className="text-red-800 m-1">*</span>
                  ) : (
                    ""
                  )}
                </label>
                {isEditing && (
                  <select
                    name="Shahr"
                    id="cities"
                    onChange={handleInputChange}
                    value={user.Shahr}
                    className={
                      ((validationHaghighi || validationHoghoghi) && user.Shahr === ""
                        ? "border-red-700"
                        : "") + " w-full border shadow rounded h-10 focus:shadow-outline"
                    }
                  >
                    {cities.length > 0 &&
                      cities.map((s) => (
                        <option className="hover:bg-teal-500" key={s.id} id={s.id} value={s.name}>
                          {s.name}
                        </option>
                      ))}
                  </select>
                )}
              </div>
            </div>
            <div className="flex flex-col lg:flex-row mt-4">
              <div className="w-full mx-3">
                <label className="block font-bold my-2">
                  معرف
                  <span className="font-medium">{!isEditing ? " :  " + user.Moaref : ""}</span>
                </label>
                {isEditing && (
                  <input
                    className="shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                    value={user.Moaref}
                    onChange={handleInputChange}
                    name={"Moaref"}
                  />
                )}
              </div>
              <div className="w-full mx-3">
                <label className="block font-bold my-2">
                  طریقه آشنایی
                  <span className="font-medium">
                    {!isEditing ? " :  " + user.NahveAshnaei : ""}
                  </span>
                </label>
                {isEditing && (
                  <select
                    name="NahveAshnaei"
                    id="NahveAshnaei"
                    onChange={handleInputChange}
                    value={user.NahveAshnaei}
                    className="w-full border shadow rounded h-10 focus:shadow-outline"
                  >
                    {nahveAshnaei.length > 0 &&
                      nahveAshnaei.map((s) => (
                        <option className="hover:bg-teal-500" key={s} id={s} value={s}>
                          {s}
                        </option>
                      ))}
                  </select>
                )}
              </div>
            </div>
            <fieldset className="shadow-md border-gray-300 border-2 p-8 pr-6 pt-4 mt-6 ">
              <legend className="text-xl font-bold ">اطلاعات حقوقی</legend>
              <div className="flex flex-col lg:flex-row">
                <div className="w-full mx-3">
                  <label
                    className={
                      (validationHoghoghi && user.NameSherkat === "" ? "text-red-800" : "") +
                      " block font-bold my-2 "
                    }
                  >
                    {" "}
                    نام شرکت
                    <span className="font-medium">
                      {!isEditing ? " :  " + (user.CodeEghtesadi !== "" ? user.NameSherkat || "" : "") : ""}
                    </span>
                    {validationHoghoghi && user.NameSherkat === "" && (
                      <span className="text-red-800 m-1">*</span>
                    )}
                  </label>
                  {isEditing && (
                    <input
                      className={
                        (validationHoghoghi && user.NameSherkat === "" ? "border-red-700" : "") +
                        " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                      }
                      value={(user.CodeEghtesadi !== "" ? user.NameSherkat || "" : "")}
                      onChange={handleInputChange}
                      name={"NameSherkat"}
                    />
                  )}
                </div>
                <div className="w-full mx-3">
                  <label
                    className={
                      (validationHoghoghi && user.ShenaseMelli === "" ? "text-red-800" : "") +
                      " block font-bold my-2 "
                    }
                  >
                    {" "}
                    شناسه ملی
                    <span className="font-medium">
                      {!isEditing ? " :  " + user.ShenaseMelli : ""}
                    </span>
                    {validationHoghoghi && user.ShenaseMelli === "" && (
                      <span className="text-red-800 m-1">*</span>
                    )}
                  </label>
                  {isEditing && (
                    <input
                      className={
                        (validationHoghoghi && user.ShenaseMelli === "" ? "border-red-700" : "") +
                        " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                      }
                      value={user.ShenaseMelli}
                      onChange={handleInputChange}
                      name={"ShenaseMelli"}
                    />
                  )}
                </div>
              </div>
              <div className="flex flex-col lg:flex-row">
                <div className="w-full mx-3">
                  <label
                    className={
                      (validationHoghoghi && user.CodePosti === "" ? "text-red-800" : "") +
                      " block font-bold my-2 "
                    }
                  >
                    کد پستی
                    <span className="font-medium">{!isEditing ? " :  " + user.CodePosti : ""}</span>
                    {validationHoghoghi && user.CodePosti === "" && (
                      <span className="text-red-800 m-1">*</span>
                    )}
                  </label>
                  {isEditing && (
                    <input
                      className={
                        (validationHoghoghi && user.CodePosti === "" ? "border-red-700" : "") +
                        " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                      }
                      value={user.CodePosti}
                      onChange={handleInputChange}
                      name={"CodePosti"}
                    />
                  )}
                </div>
                <div className="w-full mx-3">
                  <label
                    className={
                      (!formISValid &&
                      user.CodeMelli === "" &&
                      returnUrl &&
                      user.CodeEghtesadi === ""
                        ? "text-red-800"
                        : "") + " block font-bold my-2 "
                    }
                  >
                    کد اقتصادی
                    <span className="font-medium">
                      {!isEditing ? " :  " + user.CodeEghtesadi : ""}
                    </span>
                    {!formISValid &&
                      user.CodeMelli === "" &&
                      returnUrl &&
                      user.CodeEghtesadi === "" && <span className="text-red-800 m-1">*</span>}
                  </label>
                  {isEditing && (
                    <input
                      className={
                        (!formISValid &&
                        user.CodeMelli === "" &&
                        returnUrl &&
                        user.CodeEghtesadi === ""
                          ? "border-red-700"
                          : "") +
                        " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                      }
                      value={user.CodeEghtesadi}
                      onChange={handleInputChange}
                      name={"CodeEghtesadi"}
                    />
                  )}
                </div>
              </div>
              <div className="flex flex-col lg:flex-row">
                <div className="w-full mx-3">
                  <label
                    className={
                      (validationHoghoghi && user.Address === "" ? "text-red-800" : "") +
                      " block font-bold my-2 "
                    }
                  >
                    آدرس
                    <span className="font-medium">{!isEditing ? " :  " + user.Address : ""}</span>
                    {validationHoghoghi && user.Address === "" && (
                      <span className="text-red-800 m-1">*</span>
                    )}
                  </label>
                  {isEditing && (
                    <input
                      className={
                        (validationHoghoghi && user.Address === "" ? "border-red-700" : "") +
                        " shadow appearance-none border rounded w-full py-2 px-3 leading-tight focus:outline-none focus:shadow-outline"
                      }
                      value={user.Address}
                      onChange={handleInputChange}
                      name={"Address"}
                    />
                  )}
                </div>
              </div>
            </fieldset>
            {!isEditing && !returnUrl && (
              <button
                className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8 ml-4"
                type="button"
                onClick={Edit}
              >
                ویرایش اطلاعات کاربری
              </button>
            )}
            {isEditing && (
              <button
                className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8 ml-4"
                type="button"
                onClick={Save}
              >
                {returnUrl ? "ثبت و ادامه فرایند خرید" : "ثبت اطلاعات کاربری"}
              </button>
            )}
            {isEditing && !returnUrl && (
              <button
                className="bg-teal-600 hover:bg-green-600 text-white font-bold py-2 px-4 rounded-lg my-8"
                type="button"
                onClick={Cancel}
              >
                انصراف
              </button>
            )}
          </fieldset>
        </form>
        {invoices && invoices.length > 0 && (
          <div>
            {pageContext.relatedLinks && (
              <div className="w-11/12 lg:w-4/5 mx-auto">
                <div className="my-10 bg-teal-600 h-10">
                  <p className="text-white pr-3 pt-1 text-xl">نصب و راه اندازی سیستم</p>
                </div>
                <div className="my-6">
                  <span className="text-gray-600 text-lg my-4 pr-3">
                    جهت نصب و راه اندازی سیستم به لینک های زیر مراجعه نمایید.
                  </span>
                </div>
                {pageContext.relatedLinks.map((link) =>
                  link.url.startsWith("/") ? (
                    <Link
                      className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                      to={link.url}
                    >
                      {link.title}
                    </Link>
                  ) : (
                    <a
                      className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                      href={link.url}
                    >
                      {link.title}
                    </a>
                  )
                )}
                 <video className="w/full lg:w-1/2 mx-auto" controls ><source src="https://www.aparat.com/v/U3dTr" /></video>
              </div>
            )}
            {invoices.filter((invoice) => !invoice.ChandSandooghe).length > 0 && (
              <div className="w-11/12 lg:w-4/5 mx-auto my-8">
                <div className="my-10 bg-teal-600 h-10">
                  <p className="text-white pr-3 pt-1 text-xl">دانلود نرم افزار تک صندوقه</p>
                </div>
                <video className="w/full lg:w-1/2 mx-auto" controls ><source src="https://files.raymasoft.com/2023-06-14_15-40-00.mp4" /></video>
                <div className="my-6">
                  <p className="text-gray-600 text-lg my-4 pr-3">
                    در صورتیکه Net Framework 4.8. و SQL Server 2012 و یا نسخه بالاتر بر روی کامپیوتر
                    خود ندارید نسخه فایل نصب همراه با پیش نیاز را دانلود نمایید.{" "}
                  </p>
                </div>
                {singleCashDownloads.map((d) => (
                  <a
                    className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                    href={d.url}
                  >
                    {d.title}
                  </a>
                ))}
              </div>
            )}
            {invoices.filter((invoice) => invoice.ChandSandooghe).length > 0 && (
              <div className="w-11/12 lg:w-4/5 mx-auto">
                <div className="my-10 bg-teal-600 h-10">
                  <p className="text-white pr-3 pt-1 text-xl">دانلود نرم افزار چند صندوقه</p>
                </div>
                <video className="w/full lg:w-1/2 mx-auto" controls ><source src="https://files.raymasoft.com/2023-07-24_13-03-23ChandSandoghe.mp4" /></video>
                <div className="my-6">
                  <p className="text-gray-600 text-lg my-4 pr-3">
                    در صورتیکه Net Framework 4.8. و SQL Server 2012 و یا نسخه بالاتر بر روی کامپیوتر
                    خود ندارید نسخه فایل نصب همراه با پیش نیاز را دانلود نمایید.{" "}
                  </p>
                </div>
                {multiCashDownloads.map((d) => (
                  <a
                    className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg"
                    href={d.url}
                  >
                    {d.title}
                  </a>
                ))}
              </div>
            )}
            <div className="w-11/12 lg:w-4/5 mx-auto">
              <div className="my-10 bg-teal-600 h-10">
                        <p className="text-white pr-3 pt-1 text-xl"> فعالسازی نرم افزار (آفلاین)</p>
              </div>
              <div>
                <div><span> فایل activation.rvf را که از برنامه دریافت کرده اید در این قسمت بارگذاری کنید.</span></div>
                <div><input className="" style={{display:"none"}} ref={hiddenFileInput}
                    id="file" type="file" onChange={submit}  /></div>
                    <button onClick= {handleClick}  className="mx-4 my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-2 px-4 shadow-lg">بارگذاری</button>
                <div><span> سپس فایل activationCode.rsf را که پس از بارگذاری به طور خودکار دانلود می شود برای <a className="text-teal-500 bold " href="/offline-software-activation">فعالسازی نرم افزار</a> استفاده کنید.</span></div>    
              </div>        
            </div>
            <div className="my-16 w-11/12 mx-auto" id="no-more-tables">
              <div className="my-10 bg-teal-600 h-10">
                <p className="text-white pr-3 pt-1 text-xl">اطلاعات خرید شما</p>
              </div>
              <table className="whitespace-no-wrap text-sm lg:text-base mx-auto text-center border-collapse text-gray-700 w-full">
                <thead>
                  <tr className="lg:py-4 lg:px-6 bg-green-500 text-white border-2 border-green-500">
                    <th>نوع محصول</th>
                    <th>امکانات جانبی</th>
                    <th>تاریخ خرید</th>
                    <th>مبلغ پرداخت شده (تومان)</th>
                    <th>شماره پیگیری</th>
                    <th>شناسه فعالسازی</th>
                    <th>لینک دانلود بکاپ</th>
                  </tr>
                </thead>
                <tbody>
                  {invoices.map((invoice) => (
                    <tr
                      key={invoice.objectId}
                      className="whitespace-no-wrap my-8 lg:py-4 lg:px-6 border-2 lg:border-0 lg:border-b-2 border-teal-500 "
                    >
                      <td data-title="نوع محصول" className="font-sans">
                        {invoice.ProductName || <br />}
                      </td>
                      <td data-title="امکانات جانبی" className="font-sans">
                        {(invoice.EmkanatJanebi !== undefined &&
                          invoice.EmkanatJanebi.map((e) => {
                            return (
                              <span key={e}>
                                {e}
                                <br />
                              </span>
                            );
                          })) || <span>----------</span>}
                      </td>
                      <td data-title="تاریخ خرید" className="font-sans">
                        {invoice.Tarikh || <br />}
                      </td>
                      <td data-title="مبلغ پرداخت شده (تومان)" className="font-sans">
                        {invoice.paymentResult === "OK"
                          ? NumberWithCommas(invoice.MablaghTaeedShode / 10)
                          : "پرداخت ناموفق"}
                      </td>
                      <td data-title="شماره پیگیری" className="font-sans">
                        {invoice.ShomarehPeygiri || <br />}
                      </td>
                      <td data-title="شناسه فعالسازی" className="font-sans">
                        {(invoice.ShenasehFaalSazi && (
                          <>
                            {Object.keys(invoice.ShenasehFaalSazi).map((key, i) => {
                              return (
                                <span key={i}>
                                  {key === "-" ? "" : key + ": "}
                                  {invoice.ShenasehFaalSazi[key]}
                                  <br />
                                </span>
                              );
                            })}
                          </>
                        )) || <br />}
                      </td>
                      <td data-title="لینک دانلود بکاپ" className="font-sans">
                      {invoice.BackupUrl !== undefined ?  <a
                      className="my-4 block text-center lg:inline border bg-orange-500 text-white rounded-full py-1 px-6 shadow-lg"
                      href={invoice.BackupUrl} >دانلود</a>
                       : <span className="px-2">----------</span> || <br />}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default Profile;
